body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  color: #ffffff; /* Dark blue */
  background-color: #d0b0f8; /* Light blue */
  background-image: url('https://www.shutterstock.com/image-photo/womans-dreams-pretty-girl-flying-600nw-636533834.jpg');
  background-position: center; /* Center the image horizontally and vertically */
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  text-align: justify; /* Fill the entire width */
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin-bottom: 0;
}

nav {
  border-bottom: 4px solid black;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 24px;
  padding-bottom: 24px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 24px 32px;
}

nav a:hover {
  background: black;
  color: white;
}

#page-body {
  max-width: 100%; /* Fill the entire width */
  margin: 0;
  padding: 64px 0 0 0;
  text-align: justify; /* Fill the entire width */
}

.article-list-item {
  text-decoration: none;
  color: #334455; /* Dark blue */
  padding: 16px 0 0 0;
  width: 100%; /* Fill the entire width */
  text-align: justify; /* Fill the entire width */
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid #334455; /* Dark blue */
  width: 100%; /* Fill the entire width */
  text-align: justify; /* Fill the entire width */
}

#add-comment-form {
  border-bottom: 2px solid #334455; /* Dark blue */
  padding: 16px;
  width: 100%; /* Fill the entire width */
  text-align: justify; /* Fill the entire width */
}

#add-comment-form h3 {
  margin-bottom: 16px;
  font-family: 'Arial', sans-serif; /* Dreamy font */
  color: #334455; /* Dark blue */
  width: 100%; /* Fill the entire width */
  text-align: justify; /* Fill the entire width */
}

#add-comment-form input, textarea {
  display: block;
  width: 100%; /* Fill the entire width */
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid #334455; /* Dark blue */
  margin-bottom: 16px;
  text-align: justify; /* Fill the entire width */
}

#add-comment-form button {
  width: 100%; /* Fill the entire width */
  background-color: #334455; /* Dark blue */
  color: white;
  text-align: justify; /* Fill the entire width */
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
  width: 100%; /* Fill the entire width */
  text-align: justify; /* Fill the entire width */
}

.comment {
  border-bottom: 2px solid #334455; /* Dark blue */
  padding: 16px;
  width: 100%; /* Fill the entire width */
  text-align: justify; /* Fill the entire width */
}
.text-3d {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 36px;
  color: #ffffff; /* White text */
  text-shadow: 1px 1px 0 #000000, /* Layer 1 */
               2px 2px 0 #000000, /* Layer 2 */
               3px 3px 0 #000000, /* Layer 3 */
               4px 4px 0 #000000, /* Layer 4 */
               5px 5px 0 #000000; /* Layer 5 */
}
